import * as React from "react";
import {
  color,
  ColorProps,
  get,
  px,
  space,
  SpaceProps,
  style
} from "styled-system";
import styled from "styled-components";
type IconSizes = "tiny" | "small" | "medium" | "large";
type Omit<T, K extends keyof T> = Pick<T, Exclude<keyof T, K>>;
export interface Props
  extends Omit<React.SVGProps<SVGSVGElement>, "ref" | "color">,
    SpaceProps,
    ColorProps {
  iconSize?: IconSizes;
  color?: string;
}
const iconSizes = style({
  prop: "iconSize",
  cssProperty: "fontSize",
  key: "iconSizes",
  transformValue: (size, scale) => px(get(scale, size))
});

const Base = ({ iconSize, ...props }: Props) => (
  <svg
    width="1em"
    height="1em"
    viewBox="0 0 24 24"
    fill="currentcolor"
    {...props}
  >
    <polygon
      fillRule="evenodd"
      points="7 12 17 2 17 22"
      transform="matrix(-1 0 0 1 24 0)"
    />
  </svg>
);

const SvgComponent = styled(Base)`
  vertical-align: middle;
  ${space}
  ${color}
  ${iconSizes}
`;
export default SvgComponent;
